import React, { Component } from 'react';
import Navigation from '../../components/navigation';

class BBPrivete extends Component {
  componentDidMount() {
    document.body.classList.add('single-work-active');
  }
  componentWillUnmount() {
    document.body.classList.remove('single-work-active');
  }
  render() {
    const workid = 3;

    return (
      <div className="bb-privete">
        <div className="content">
          <h2 className="single-work__title">BB Privete</h2>
          <p>O Lorem Ipsum  um texto modelo da indstria tipogrfica e de</p>
          <p>impresso. O Lorem Ipsum tem vinda ser o texto padro usado por</p>
          <p>estas indstrias desde o ano de 1500, quando uma misturou os</p>
          <p>caracteres de um texto para ri um espcime de livro. Este texto</p>
          <p>no s sobreviveu 5 sculos, s tambm o salto para a tipografia</p>
          <p>electrnica, mantendossencialmente inalterada. Foi popularizada</p>
          <p>nos anos 60 com a disponibizao das folhas de Letraset, que</p>
          <p>continham passagens com Lorem Ipsum, e mais recentemente com os</p>
          <p>programas de publio como o Aldus PageMaker que incluem verses</p>
          <p>do Lorem Ipsum.</p>
        </div>
        <Navigation workid={workid} />
      </div>
    );
  }
}

export default BBPrivete;



